// channels 的仓库数据
// import { getNewsChannels } from "../services/newsService"
import service from "@/services/index.js"

export default {
    namespaced: true, //开启命名空间
    //配置共享数据
    state:{
        existZip: false,
        downloadUrl: "",
    },
    mutations:{
        //这里配置多种编译方式
        //state:原来的状态； payload:负荷；
        setExistZip(state, payload){
            state.existZip = payload;
        },
        setDownloadUrl(state, payload){
            state.downloadUrl = payload;
        },
    },
    // 有副作用操作的函数: context仓库对象；$store
    // 不能修改数据，只能处理副作用；
    actions:{
        async fetchDatas(context, payload) {
            // // 设置isLoading 为true
            // context.commit("setIsLoading", true);
            // 设置data为channels
            var url = await service.getDownLoadUrl(payload);
            context.commit("setDownloadUrl", url);
            // // 设置isLoading 为false
            // context.commit("setIsLoading", false);
        }
    },
}