<template>
  <div style="
    background-color: #09f; color:#fff;
    width:100%; height:100px;
    border-radius: 1%;
  ">
      <ul>
          <li><img :src="logo.src" :width="logo.width" :height="logo.height" ></li>
          <li v-for="item in data" :key="item.pageName" >
            <router-link @click.native="setCurrentPage(item.pageName)"
            :to="{name: item.pageName}">
                <h2 v-if="currentPage==item.pageName" style="color:red">
                    {{ item.titleName }}
                </h2>
                <h2 v-else style="color:yellow">
                    {{ item.titleName }}
                </h2>
            </router-link>
          </li>
          <!-- <li style="font-size:1rem;">登录</li>
          <li style="font-size:1rem; margin:0 0px;">注册</li> -->
      </ul>
  </div>
</template>

<script>
import { mapState } from "vuex"
import store from "@/store/index.js"

//data 是Vue实例的数据对象。
//computed，计算属性将被混入到 Vue 实例中。
//props 是props 可以是数组或对象，用于接收来自父组件的数据。
export default {
    //计算属性
    computed:{
        //组件中使用仓库的数据
        ...mapState("channels", ["logo", "data", "isLoading", "currentPage"]),
    },  
    methods:{
        setCurrentPage(param) {
            store.commit('channels/setCurrentPage', param);
        },
        handleHeader(title){
            console.log(title);
        },
    },
    created(){
        // console.log(window.location)
        // console.log(this.$route)
        // console.log(this.$router)
        // 
        var pathName = window.location.pathname;
        var array = this.$router.options.routes;
        var pageName = ""
        array.forEach(element => {
            if (element.path == pathName) {
                pageName = element.name
            }
        });
        this.setCurrentPage(pageName);
    },
}
</script>

